html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

.icon-arrow {
  width: 1em;
  height: 1em;
  fill: none;
}

.icon-facebook {
  width: 0.53em;
  height: 1em;
  fill: #000;
}

body {
  background: #FFF;
  font-size: 50px;
  font-size: calc(16px + (50 - 16) * ((100vw - 600px) / (1000 - 600)));
  line-height: 1.4;
}

@media (max-width: 600px) {
  body {
    font-size: 16px;
  }
}

@media (min-width: 1000px) {
  body {
    font-size: 50px;
  }
}

@media screen and (max-width: 664px) {
  .header__menu {
    position: fixed;
    top: 70px;
    right: 0;
    width: 0%;
    height: 50vh;
    z-index: -1;
    display: block;
    opacity: 0;
  }
  .header__menu ul {
    position: absolute;
    top: 10vh;
    right: 0;
    padding-left: 40px;
    background: linear-gradient(to left, rgba(0, 0, 0, 0.85) 70%, transparent);
  }
  .header__menu ul li {
    text-align: right;
  }
  .header nav ul li a {
    padding: 15px;
    opacity: 0;
    font-size: 16px;
    font-weight: 600;
    letter-spacing: 1.2px;
    transition: all 0.2s ease;
    display: none;
  }
  .header__menu ul li:not(:first-child) {
    margin-left: 0;
  }
  .nav-btn {
    position: fixed;
    right: 10px;
    top: 30px;
    display: block;
    width: 48px;
    height: 48px;
    cursor: pointer;
    z-index: 9999;
    border-radius: 50%;
    background: radial-gradient(rgba(255, 255, 255, 0.6) 10%, transparent 60%);
  }
  .nav-btn i {
    display: block;
    width: 20px;
    height: 2px;
    background: #fff;
    border-radius: 2px;
    margin-left: 14px;
  }
  .nav-btn i:nth-child(1) {
    margin-top: 16px;
  }
  .nav-btn i:nth-child(2) {
    margin-top: 4px;
    opacity: 1;
  }
  .nav-btn i:nth-child(3) {
    margin-top: 4px;
  }
}

#nav:checked + .nav-btn {
  transform: rotate(45deg);
  background: radial-gradient(rgba(255, 255, 255, 0.6) 10%, transparent 60%);
}

#nav:checked + .nav-btn i {
  background: #fff;
  transition: transform 0.2s ease;
}

#nav:checked + .nav-btn i:nth-child(1) {
  transform: translateY(6px) rotate(180deg);
}

#nav:checked + .nav-btn i:nth-child(2) {
  opacity: 0;
}

#nav:checked + .nav-btn i:nth-child(3) {
  transform: translateY(-6px) rotate(90deg);
}

#nav:checked ~ .header__menu {
  opacity: 1;
  transition: all 1.3s ease;
}

#nav:checked ~ .header__menu ul li {
  display: block;
  padding: 5px;
}

#nav:checked ~ .header__menu ul li a {
  transition: 1.2s ease;
  opacity: 1;
  display: block;
  transform: translateX(0);
}

.hidden {
  display: none;
}

.block {
  display: flex;
}

body {
  font-family: 'Open Sans', sans;
  color: #FFF;
}

.container {
  max-width: 1050px;
  margin: 0 auto;
}

@media only screen and (max-width: 1080px) {
  .container {
    padding-left: 30px;
    padding-right: 30px;
  }
}

.wrap {
  height: 931px;
  width: 100%;
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  position: relative;
}

.wrap:before {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  height: 931px;
  background: rgba(0, 0, 0, 0.8);
}

.header {
  z-index: 1000;
  margin-bottom: 5%;
  position: relative;
}

@media only screen and (max-width: 1000px) {
  .header {
    margin-bottom: 10%;
  }
}

@media only screen and (max-width: 600px) {
  .header {
    margin-bottom: 10%;
  }
}

.header .container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding-top: 30px;
}

@media only screen and (max-width: 800px) {
  .header .container {
    flex-direction: column;
  }
}

.logo {
  text-transform: uppercase;
  border: 3px solid #fff;
  line-height: .9;
  margin-right: 20px;
  font-size: 32px;
  min-width: 145px;
}

@media only screen and (max-width: 800px) {
  .logo {
    margin-bottom: 30px;
    margin-right: 0;
  }
}

.logo__part1, .logo__part2 {
  padding: 7px;
  display: inline-block;
}

.logo__part1 {
  background: #FFF;
  color: #000;
  font-weight: bold;
  padding: 7px 10px;
}

.logo__part2 {
  padding-left: 0px;
  padding-right: 0px;
}

.header nav ul {
  font-size: 13px;
  text-transform: uppercase;
}

.header nav ul li {
  display: inline;
  margin-left: 45px;
  box-sizing: border-box;
}

@media only screen and (max-width: 940px) {
  .header nav ul li {
    margin-left: 25px;
    padding-bottom: 15px;
  }
}

.header nav ul li a {
  text-align: center;
  font-family: 'Open Sans', sans;
  text-decoration: none;
  color: #FFF;
  border-bottom: 2px solid transparent;
  transition: .25s;
}

.header nav ul li a:hover, .header nav ul li a:active {
  border-bottom: 2px solid red;
  color: red;
}

.section {
  margin: 0 auto;
  position: relative;
  flex: 1;
  text-align: center;
}

.section .container {
  position: relative;
  width: 90vw;
}

.section__suptitle {
  text-align: center;
  font-size: 18px;
  margin-bottom: 18px;
}

.section__title {
  margin: 0 auto;
  font-size: 63px;
  font-weight: bold;
  position: relative;
  display: inline-block;
  padding-bottom: 19px;
  margin-bottom: 50px;
}

.section__title:before, .section__title:after {
  width: 35%;
  height: 2px;
  background: #FFF;
  position: absolute;
  content: "";
  bottom: -8px;
}

.section__title:before {
  left: 10%;
}

.section__title:after {
  right: 10%;
}

@media only screen and (max-width: 500px) {
  .section__title {
    font-size: 2.5rem;
    width: 80%;
  }
}

.section__point {
  background: red;
  width: 5px;
  height: 5px;
  border-radius: 50%;
  position: absolute;
  bottom: -10px;
  left: 50%;
  transform: translate(-50%, 0);
}

.section__text {
  width: 50%;
  font-size: 14px;
  margin: 0 auto;
  margin-bottom: 40px;
  line-height: 2;
}

@media only screen and (max-width: 800px) {
  .section__text {
    width: 80%;
  }
}

@media only screen and (max-width: 600px) {
  .section__text {
    width: 100%;
  }
}

.section__btns {
  font-size: 14px;
  font-weight: bold;
  max-width: 430px;
  display: flex;
  margin: 0 auto;
  justify-content: space-between;
  margin-bottom: 50px;
}

@media only screen and (max-width: 500px) {
  .section__btns {
    flex-direction: column;
    align-items: center;
  }
}

.section__btn {
  display: inline-block;
  line-height: 47px;
  border: 1px solid #fff;
  border-radius: 2px;
  width: 200px;
  cursor: pointer;
  text-decoration: none;
  color: #FFF;
  transition: .25s ease-out;
}

.section__btn:first-of-type {
  margin-right: 0;
}

.section__btn:hover {
  background: red;
  border: 1px solid red;
  border-radius: 5px;
}

@media only screen and (max-width: 600px) {
  .section__btn {
    margin-bottom: 30px;
    width: 180px;
  }
}

@media only screen and (max-width: 500px) {
  .section__btn {
    width: 200px;
  }
}

.anchor {
  flex: none;
  position: relative;
  height: 100px;
  width: 100%;
  box-sizing: border-box;
}

.anchor:before, .anchor:after {
  content: '';
  background: #FFF;
  width: 40%;
  height: 100px;
  position: absolute;
  bottom: -20px;
  transform: skewY(3deg);
}

.anchor:after {
  right: 0;
  transform: skewY(-3deg);
}

.anchor__chamfer {
  transform: skewY(8deg);
  width: calc(50% + 50px);
  position: absolute;
  height: 100px;
  top: -15px;
  bottom: -50px;
  background: #fff;
  height: 100%;
  z-index: 30;
}

.anchor__chamfer-right {
  transform: rotate(0deg) skewY(-8deg);
  right: 0;
}

.anchor__img:before {
  content: '';
  position: absolute;
  width: 73px;
  height: 73px;
  left: -4px;
  top: -4px;
  border-radius: 50%;
  background: rgba(204, 204, 204, 0.5);
  z-index: 29;
  left: 50%;
  transform: translate(-50%, calc(2.9vw - 50px));
}

.anchor__img-inside {
  position: absolute;
  width: 65px;
  height: 65px;
  left: 50%;
  transform: translate(-50%, calc(3vw - 50px));
  z-index: 30;
  background: #FFF;
  border-radius: 50%;
}

.anchor__bottom-white {
  position: absolute;
  width: 100%;
  height: 50px;
  left: 0;
  bottom: 0;
  background: #FFF;
}
